/* Colors and default size */
:root {
  --primaryColor: #7bd9d8;
  --primaryHover: hsl(185, 92%, 41%);
  --secondaryColor: rgb(150, 162, 188);
  --paragraphColor: rgb(127, 127, 175);
  --BgColor: #031a30;
  --projectCardColor: #072340;
  --textColor: #dae4fb;
  --headerHeight: 3rem;
}

.skills .skillsContainer {
  gap: 2rem 0;
  padding: 2rem 0;
}
.skills .skillsContainer .skillGroup .groupTitle {
  color: var(--textColor);
  padding: 0.3rem 0 2rem;
}
.skills .skillsContainer .skillGroup .generalSkills {
  display: grid;
  align-items: center;
  justify-content: center;
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
}
.skills .skillsContainer .skillGroup .generalSkills .singleSkill {
  display: grid;
  align-items: center;
  justify-content: center;
}
.skills .skillsContainer .skillGroup .generalSkills .singleSkill .iconBox {
  position: relative;
  height: 70px;
  width: 60px;
  background: var(--projectCardColor);
  border: 2px solid var(--secondaryColor);
  border-radius: 5px;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  z-index: 500;
}
.skills .skillsContainer .skillGroup .generalSkills .singleSkill .iconBox .icon {
  font-size: 35px;
  z-index: 500;
}
.skills .skillsContainer .skillGroup .generalSkills .singleSkill .iconBox::before {
  content: "";
  height: 0;
  width: 100%;
  background: var(--primaryColor);
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  transition: 0.2s;
}
.skills .skillsContainer .skillGroup .generalSkills .singleSkill .iconBox::after {
  content: "";
  height: 0;
  width: 100%;
  background: var(--BgColor);
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  transition: 0.5s;
}
.skills .skillsContainer .skillGroup .generalSkills .singleSkill .iconBox:hover {
  color: var(--textColor);
  border: 2px solid var(--primaryColor);
}
.skills .skillsContainer .skillGroup .generalSkills .singleSkill .iconBox:hover .icon {
  color: var(--primaryColor);
}
.skills .skillsContainer .skillGroup .generalSkills .singleSkill .iconBox:hover::before,
.skills .skillsContainer .skillGroup .generalSkills .singleSkill .iconBox:hover::after {
  height: 100%;
}
.skills .skillsContainer .skillGroup .generalSkills .singleSkill .skillName {
  padding-top: 0.3rem;
  text-align: center;
  font-weight: 600;
  color: var(--secondaryColor);
}

@media screen and (min-width: 769px) {
  .skills .skillsContainer .skillGroup .generalSkills {
    grid-template-columns: repeat(4, 1fr);
  }
}